<template>
  <div class="comtainer">
    <van-row gutter="30">
      <van-col span="8" class="layout">span: 8</van-col>
      <van-col span="8" class="layout">span: 8</van-col>
      <van-col span="8" class="layout">span: 8</van-col>
    </van-row>
    <van-button type="default">默认按钮</van-button>
    <van-badge-group :active-key="activeKey">
    <van-badge title="标签名称" @click="onClick" info="8" />
    </van-badge-group>
    <van-card
      title="标题"
      desc="描述"
      num="2"
      price="2.00"
      :thumb="imageURL"
    />
    <van-picker :columns="columns" @change="onChange"  />
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <p>刷新次数: {{ count }}</p>
    </van-pull-refresh>
    <van-actionsheet v-model="show" :actions="actions" />

    <van-uploader :after-read="onRead">
      <van-icon name="photograph" />
    </van-uploader>

    <van-search
      v-model="value"
      show-action
      @search="onSearch"
    >
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>

  </div>
</template>
<script>
import { Dialog } from 'vant';


export default{
  name: 'Zaui', 
  data(){
    return{
      activeKey:0,
      columns: ['杭州', '宁波', '温州', '嘉兴', '湖州'],
      imageURL:'',
      isLoading:false,
      count:0,
      show: false,
      value:'',
      actions: [
        {
          name: '选项',
          callback: this.callback
        },
        {
          name: '选项',
          subname: '描述信息'
        },
        {
          name: '选项',
          loading: true
        }
      ],
    }
  },
  methods:{
    onClick(key) {
      Dialog.alert({
        title: '标题',
        message: '弹窗内容'
      }).then(() => {
        // on close
      });
      this.show=true;
      this.activeKey = key;
      
    },
    onChange(){

    },
    onRefresh(){
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
        this.count++;
      }, 500);
    },
    callback(){
      this.show=false;
    },
    onRead(file) {
      console.log(file)
    },
    onSearch(){

    }
  }

}
  

</script>

<style scoped>
  
  .page{
    position: fixed;
    bottom: 30px;
    width: 100%;
    height: 50px;
    text-align: center;
  }
  .page .prev{
    margin-right: 30px;
  }
  .layout{
    background-color: blue;
  }
</style>


