<!-- 发现 -->

<template>
  <div class="find">
    <div v-for="item in findListData">
      <find-item :data="item"></find-item>
    </div>

    <tab-bar></tab-bar>
  </div>
</template>

<script>
import axios from 'axios'
import TabBar from '@/components/base/tab-bar/tab-bar'
import FindItem from '@/components/base/find-item/find-item'

export default {
  components: {
    TabBar,
    FindItem
  },
  data () {
    return {
      findListData: [
        {
          title:'大家看风景',
          view:'回头是岸',
          img:require('../../assets/img/image.jpg')
        },
        {
          title:'大家看风景',
          view:'回头是岸'
        }
      ]
    }
  },
  props: {},
  watch: {},
  methods: {
    _initFindListData () {
      axios.get('/api/findList').then(res => {
        if (res.data.code === 0) {
          this.findListData = res.data.data.data
        }
      }).catch(err => {
        console.log(err)
      })
    }
  },
  filters: {},
  computed: {},
  // created () {
  //   this._initFindListData()
  // },
  mounted () {},
  destroyed () {}
}
</script>

<style scoped>

.find {
  margin-bottom: 50px;
}
</style>



