<template>
<!-- 首页 -->
<div class="index">
  <!-- 轮播图 -->
  <div class="slider">
    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="item in swipeData" :key="item.pic">
            <img :src="item.pic">
      </mt-swipe-item>
    </mt-swipe>
  </div>

  <!-- 九宫格 -->
  <div class="types">
      <types-item v-for="item in types"
                  :key="item.ico"
                  :ico="item.ico"
                  :txt="item.txt" @toList="toList(item)">
      </types-item>
  </div>
  
  <!-- 底部导航栏 组件 -->
  <tab-bar></tab-bar>
</div>

</template>

<script>
import TabBar from '@/components/base/tab-bar/tab-bar'
import TypesItem from '@/components/base/types-item/types-item'

export default {
  name: 'Index',
  components: {
    TypesItem,
    TabBar
  },
  data () {
    return {
      swipeData: [
        {
          pic: require('./img/swipe/bannertemp.e8a6fa63.jpg')
        }
      ],
      types: [
        {
          ico: require('./img/types/types (7).png'),
          txt: '美食'
        },
        {
          ico: require('./img/types/types (0).png'),
          txt: '美团超市'
        },
        {
          ico: require('./img/types/types (1).png'),
          txt: '生鲜果蔬'
        },
        {
          ico: require('./img/types/types (5).png'),
          txt: '甜点饮品'
        },
        {
          ico: require('./img/types/types (4).png'),
          txt: '正餐优选'
        },
        {
          ico: require('./img/types/types (2).png'),
          txt: '美团专送'
        },
        {
          ico: require('./img/types/types (3).png'),
          txt: '能量西餐'
        },
        {
          ico: require('./img/types/types (6).png'),
          txt: '精品小吃'
        }
      ],
      indexList: []
    }
  },
  methods:{
    toList(item){
      if(item.txt=="美食"){
        this.$router.push('/goods');
        return;
      }
      this.$router.push('/test');
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.index .slider{
   
    height: 170px;
    font-size: 30px;
    text-align: center;
    overflow: hidden;
    
  }
.index img {
      width: 100%;
      height: 100%;
}

</style>



