<template>
  <div class="comtainer">
    <div>
      <mt-header title="物资清单">
        <router-link to="/goods" slot="left">
          <mt-button icon="back">返回</mt-button>
        </router-link>
        <mt-button slot="right" @click.native="saveInfo">完成</mt-button>
      </mt-header>
    </div>
    <div class="table-comtainer">
        <table class="table-content"  width="500">
            <thead>
                <tr>
                    <th width="220">物资名称</th>
                    <th width="100">系统数量</th>
                    <th width="100">清点数量</th>
                    <th width="100">完整</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in msg" 
                  v-if="pageSize*(nowPageNum-1)<=index && index<pageSize*nowPageNum"> 
                  <td><img src="./100x100.png">{{item.goodsName}}</td>
                  <td><span>{{item.sysNumber}}</span></td>
                  <td><input type="text" v-model="item.checkNumber" size="4"></td>
                  <td class="btn-switch">
                    <mt-switch v-model="item.status" @change="switchHandle(index)" >&nbsp;&nbsp;
                    </mt-switch>
                  </td>
                </tr> 

            </tbody>

        </table>
    </div>
    <div class="page" v-if="isPage">
      <mt-button type="default" class="btn prev" @click.native="pervPage" :disabled="!perv">上一页</mt-button> 
      <mt-button type="default" class="btn next" @click.native="nextPage" :disabled="!next">下一页</mt-button>
    </div>
  </div>
</template>

<script> 
import myData from '../data/goodsItem.json';
import { MessageBox } from 'mint-ui';
import { Indicator } from 'mint-ui';
export default{
  data(){
    return{
      msg:"",//数据
      isPage:false,//是否有分页
      totalSize:0,//总条数
      pageSize:10,//每页条数
      totalPageNum:1,//总页数
      nowPageNum:1,//当前页码
      perv:false,//是否有上一页 默认否
      next:false,//是否有下一页
    }
  },
  methods:{
    saveInfo(){
      this.axios.post('/test/goodsItem',this.msg).then(resp=>{
        console.log(resp.data.success);
      })
     console.log(JSON.stringify(this.msg));
    },
    switchHandle(index){
      if(this.msg[index].status==true){
        this.msg[index].checkNumber= this.msg[index].sysNumber;
      }else{
          if(this.msg[index].checkNumber==this.msg[index].sysNumber){
            MessageBox.alert("清点正确按钮状态不能改变", "提示").then((action)=>{
            this.msg[index].status=true;
            }); 
            
          }
      }
    },
    pervPage(){
      this.next=true;
      if(this.nowPageNum!=1){
          this.nowPageNum=this.nowPageNum-1;
      }
      if(this.nowPageNum==1){
          this.perv=false;
      }
    },
    nextPage(){
      this.perv=true;
      if(this.nowPageNum!=this.totalPageNum){
        this.nowPageNum=this.nowPageNum+1;
      }
      if(this.nowPageNum==this.totalPageNum){
           this.next=false;
      }
      console.log(this.perv);
    }
  },
  mounted(){
    var _this=this;
    Indicator.open('加载中...');
    _this.getRequest("/goodsItem/"+this.$route.params.typeId).then(resp=>{
        _this.msg=resp.data.msg;
        Indicator.close();
    }).catch(function(error){
       Indicator.close();
    });
    this.totalSize=this.msg.length;
      if(this.totalSize>10) {
        this.totalPageNum=parseInt((this.totalSize+this.pageSize-1)/this.pageSize);
        this.isPage=true
        this.next=true;
        console.log(this.isPage);
        console.log(this.next);
      }
  },
  computed: {
    // 计算属性的 getter

    
  },
  watch:{
    msg:{
      handler: function (val, oldVal) {         
          for(var i=0;i<val.length;i++){             
            if(val[i].sysNumber==val[i].checkNumber){
              if (!val[i].status) {
                console.log(i+":"+val[i].status);
                this.msg[i].status=true;
              }
            }else{
              this.msg[i].status=false;
            }
          };
          
      },
      deep: true
    },
    user(val){
      console.log(val);
    },
  }

}
  

</script>
<style scoped>
.comtainer{
    text-align: center;
  }
  .table-content{
    width: 100%;
    height: auto;
    font-size: 12px;
    margin-top: 20px;
  }
  tr td{
    text-align: center;
    height: 42px;
  }
  tr td input{
    text-align: center;
  }
  .mint-switch-core{
    padding-left: 20px;
  }
  .btn-switch{
    padding-left: 20px;
  }
  .page{
    position: fixed;
    bottom: 30px;
    width: 100%;
    height: 50px;
  }
  .page .prev{
    margin-right: 30px;
  }
  img{
    width: 1rem;
    height: auto;
    margin-right: 3px;
    display: inline-block;
    vertical-align: middle;
  }
</style>


