<template>
<div>
    <!-- <div class="header">
      <mt-header fixed title="固定在顶部"></mt-header>
    </div> -->
    <mt-tabbar v-model="selected">
  <mt-tab-item id="外卖">
    <img slot="icon" src="./100x100.png">
    外卖
  </mt-tab-item>
  <mt-tab-item id="订单">
    <img slot="icon" src="./100x100.png">
    订单
  </mt-tab-item>
  <mt-tab-item id="发现">
    <img slot="icon" src="./100x100.png">
    发现
  </mt-tab-item>
  <mt-tab-item id="我的">
    <img slot="icon" src="./100x100.png">
    我的
  </mt-tab-item>
</mt-tabbar>
    
    <div>
    <mt-search
      v-model="searchValue"
      cancel-text="取消"
      placeholder="搜索"
      :show="true">
      <mt-cell
        v-for="item in searchResult"
        :title="item.title"
        :value="item.value">
      </mt-cell>
    </mt-search>
    </div>
    <mt-switch v-model="switchValue">开关</mt-switch>
    <div>
      <mt-checklist
        title="复选框列表"
        v-model="checkListValue"
        :options="options">
      </mt-checklist>
    </div>
    <mt-radio
      title="单选框列表"
      v-model="radioValue"
      :options="options">
    </mt-radio>
    <mt-cell-swipe
      title="标题文字2"
      :right="[
        {
          content: 'Delete',
          style: { background: 'red', color: '#fff' },
          handler: () => this.$messagebox('delete')
        }
      ]">
    </mt-cell-swipe>
    <mt-spinner type="snake" color="#26a2ff" :size="60">snake</mt-spinner>
    <mt-spinner type="double-bounce" color="#26a2ff">double-bounce</mt-spinner>
    <mt-spinner type="triple-bounce" color="#26a2ff">triple-bounce</mt-spinner>

    <mt-cell title="标题文字"> <mt-spinner type="fading-circle" color="#26a2ff">fading-circles</mt-spinner></mt-cell>
    <mt-cell title="标题文字" value="说明文字">说明
        <mt-badge size="small" type="error">30
        </mt-badge>
    </mt-cell>

    <mt-field 
      label="自我介绍" 
      placeholder="自我介绍" 
      type="textarea" 
      rows="4" 
      v-model="introduction">
      
    </mt-field>

    <mt-navbar v-model="selected">
      <mt-tab-item id="1">选项一</mt-tab-item>
      <mt-tab-item id="2">选项二</mt-tab-item>
      <mt-tab-item id="3">选项三</mt-tab-item>
    </mt-navbar>
    <div class="tab-container">
    <!-- tab-container -->
    <mt-tab-container v-model="selected" :swipeable="true">
      <mt-tab-container-item id="1">
        <mt-cell v-for="n in 10" :title="'内容 ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <mt-cell v-for="n in 4" :title="'测试 ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="3">
        <mt-cell v-for="n in 6" :title="'选项 ' + n" />
      </mt-tab-container-item>
    </mt-tab-container>
    </div>

    <!-- <mt-tabbar v-model="tabbar" :fixed="true">
      <mt-tab-item id="外卖">
        <img slot="icon" src="./index/img/types/types (7).png">
        外卖
      </mt-tab-item>
      <mt-tab-item id="订单">
        <img slot="icon" src="./index/img/types/types (7).png">
        订单
      </mt-tab-item>
      <mt-tab-item id="发现">
        <img slot="icon" src="./index/img/types/types (7).png">
        发现
      </mt-tab-item>
      <mt-tab-item id="我的">
        <img slot="icon" src="./index/img/types/types (7).png">
        我的
      </mt-tab-item>
    </mt-tabbar>
    <mt-tab-container v-model="tabbar" :swipeable="true">
      <mt-tab-container-item id="外卖">
        <mt-cell v-for="n in 10" :title="'外卖 ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="订单">
        <mt-cell v-for="n in 4" :title="'订单 ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="发现">
        <mt-cell v-for="n in 6" :title="'发现 ' + n" />
      </mt-tab-container-item>
       <mt-tab-container-item id="我的">
        <mt-cell v-for="n in 6" :title="'我的 ' + n" />
      </mt-tab-container-item>
    </mt-tab-container> -->
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>

    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" 
    :bottom-all-loaded="allLoaded" ref="loadmore">
      <ul>
        <li v-for="item in list">{{ item }}</li>
      </ul>
    </mt-loadmore>

    <ul>
      <li v-for="item in listItem">
        <img v-lazy="item">
      </li>
    </ul>
    <mt-progress :value="progressValue">
      <div slot="start">0%</div>
      <div slot="end">100%</div>
    </mt-progress>

    <mt-range
      v-model="rangeValue"
      :min="0"
      :max="100"
      :step="10"
      :bar-height="5"
      @change="rangeChange">
      <div slot="start">0</div>
      <div slot="end">100</div>
    </mt-range>
    <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    <mt-datetime-picker
      v-model="pickerVisible"
      type="date"
      ref="picker"
      year-format="{value} 年"
      month-format="{value} 月"
      date-format="{value} 日"
      @confirm="handleConfirm">
    </mt-datetime-picker>
        <mt-index-list>
      <mt-index-section index="A">
        <mt-cell title="Aaron"></mt-cell>
        <mt-cell title="Alden"></mt-cell>
        <mt-cell title="Austin"></mt-cell>
      </mt-index-section>
      <mt-index-section index="B">
        <mt-cell title="Baldwin"></mt-cell>
        <mt-cell title="Braden"></mt-cell>
      </mt-index-section>
      <mt-index-section index="Z">
        <mt-cell title="Zack"></mt-cell>
        <mt-cell title="Zane"></mt-cell>
      </mt-index-section>
    </mt-index-list>
    <div>


   
    <mt-palette-button content="+" @expand="main_log('expand')" @expanded="main_log('expanded')" @collapse="main_log('collapse')"
      direction="rt" class="pb" :radius="80" ref="target_1" mainButtonStyle="color:#fff;background-color:#26a2ff;"
      style="left:30px;">
      <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(1)"></div>
      <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(2)"></div>
      <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(3)"></div>
    </mt-palette-button>
    </div>

    <button @click='popup'>弹出框</button>
    <button @click='active'>弹出 action sheet</button>
    <button @click='toast'>弹出toast</button>
    <button @click='indicator'>弹出indicator</button>
    <button @click='openPicker'>弹出日期控件</button>
    <mt-button size="small">small</mt-button>
    <mt-button size="large">large</mt-button>
    <mt-button size="normal">normal</mt-button>
    <mt-button type="default">default</mt-button>
    <mt-button type="primary">primary</mt-button>
    <mt-button type="danger">danger</mt-button>



   
</div>
</template>
<script>
import { MessageBox,Toast,Indicator   } from 'mint-ui';

export default {
    name: 'HelloWorld',
    data () {
        return {
            searchValue:"",
            msg: 'Welcome to Your Vue.js App',
            list:[
              '简单',
              '很简单'
            ],
            allLoaded:false,
            actions:[
            {
                name:'测试1',
                method: this.actionsheet//回调函数
            },
          ],
          sheetVisible:false,
          cancelText:'撤销', //设置空不显示

          rangeValue:70,
          listItem:[
            require('./index/img/types/types (7).png')
          ],
          progressValue:90,

          slots: [
              {
                flex: 1,
                values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                className: 'slot1',
                textAlign: 'right'
              }, {
                divider: true,
                content: '-',
                className: 'slot2'
              }, {
                flex: 1,
                values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                className: 'slot3',
                textAlign: 'left'
              }
            ],
          pickerVisible:"", 
          selected:"2",//默认显示的id
          tabbar:"我的",//默认显示的id
          searchResult:[
            {
              title:'很帅1',
              value:'很酷1'
            },
            {
              title:'很帅2',
              value:'很酷2'
            },
            {
              title:'很帅3',
              value:'很酷3'
            }
          ],
          options:[
            {
              label: '被禁用',
              value: '值F',
              disabled: true
            },
            {
              label: '选中禁用',
              value: '选中禁用的值',
              disabled: true
            },
            {
              label: '选项A',
              value: '值A'
            },
            {
              label: '选项B',
              value: '值B'
            }
          ],
          checkListValue:[],
          radioValue:"",
          switchValue:"",
          introduction:"",

        }
                 
    },

    methods: {

      popup(){
        console.log('dsd');
        MessageBox.confirm('确定执行此操作?').then(action => {
          console.log(action);
        });
      },
      loadTop() {
        // 加载更多数据
        this.list.push("添加1");
        this.list.push("添加2");
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom() {
        // 加载更多数据
        this.list.push('添加4');
        this.allLoaded = true;// 若数据已全部获取完毕
        this.$refs.loadmore.onBottomLoaded();
      },
    


      actionsheet(){
         console.log('回调函数');
      }, 
      active(){
        this.sheetVisible=true
      },
      toast(){
        Toast({
          message: '提示',
          position: 'bottom',
          duration: 5000,
          //iconClass: 'icon icon-success'  //图片需自行准备
        });
      },
      indicator(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'snake'//'snake''fading-circle''double-bounce''triple-bounce'
        });
        
        setTimeout(function () {
            Indicator.close();
          }, 1000);
      },
      rangeChange(){
        console.log(this.rangeValue);
      },
      onValuesChange(picker, values) {
        if (values[0] > values[1]) {
          console.log(values);
          picker.setSlotValue(1, values[0]);
        }
      },
      openPicker() {
        this.$refs.picker.open();
      },
      handleConfirm(value){
        console.log(value);
        console.log(this.pickerVisible);
      },
       main_log(val) {
        console.log('main_log', val);
      },
      sub_log(val) {
        console.log('sub_log', val);
        this.$refs.target_1.collapse();
      },
      

  }
}
</script>
<style scoped>
  .header{
    width: 100%;
    height: 50px;
  }
</style>


