<template>
  <div class="comtainer">
    <div>
      <mt-header title="物资清单">
        <router-link to="/" slot="left">
          <mt-button icon="back">返回</mt-button>
        </router-link>
        <mt-button slot="right" @click="saveInfo">完成</mt-button>
      </mt-header>
    </div>
    <div class="page-cell" v-for="(item,index) in msg"   
          v-if="pageSize*(nowPageNum-1)<=index && index<pageSize*nowPageNum">

          <mt-cell  v-if="item.isLink"
              :title="item.typeName" 
              :to="'/goodsItem/'+item.typeId"
              is-link>
            <img slot="icon" :src="item.src=='' || !item.src?imgSrc:item.src"  width="24" height="24">
          </mt-cell>

          <mt-cell :title="item.typeName" v-else>
            <img slot="icon" :src="item.src=='' || item.src=='null'?imgSrc:item.src" width="24" height="24">
            <mt-switch v-model="item.status" ></mt-switch>
          </mt-cell>
    </div>
    <div class="page" v-if="isPage">
      <mt-button type="default" class="btn prev" @click.native="pervPage" :disabled="!perv">上一页</mt-button> 
      <mt-button type="default" class="btn next" @click.native="nextPage" :disabled="!next">下一页</mt-button>
    </div>
  </div>
</template>
<script>
import myData from '../data/goods.json';
import imgSrc from './100x100.png';
import { Indicator } from 'mint-ui';

export default{
  data(){
    return{
      msg:"",
      imgSrc: imgSrc,
      isPage:false,//是否有分页
      totalSize:0,//总条数
      pageSize:10,//每页条数
      totalPageNum:1,//总页数
      nowPageNum:1,//当前页码
      perv:false,//是否有上一页 默认否
      next:false,//是否有下一页

    }
  },
  methods:{
    saveInfo(){
     //  this.axios.post('/test/goods',this.msg).then(resp=>{
     //    console.log(resp.data.success);
     //  })
     // console.log(JSON.stringify(this.msg));
    },
     pervPage(){
      this.next=true;
      if(this.nowPageNum!=1){
          this.nowPageNum=this.nowPageNum-1;
      }
      if(this.nowPageNum==1){
          this.perv=false;
      }
    },
    nextPage(){
      this.perv=true;
      if(this.nowPageNum!=this.totalPageNum){
        this.nowPageNum=this.nowPageNum+1;
      }
      if(this.nowPageNum==this.totalPageNum){
           this.next=false;
      }
      console.log(this.perv);
    }
  },
  mounted(){
    Indicator.open('加载中...');
    var _this=this;
    _this.getRequest("/goods").then(resp=>{
        _this.msg=resp.data.msg;
        Indicator.close();
    }).catch(function(error){

         Indicator.close();
    });

    this.totalSize=this.msg.length;
      if(this.totalSize>10) {
        this.totalPageNum=parseInt((this.totalSize+this.pageSize-1)/this.pageSize);
        this.isPage=true
        this.next=true;
        console.log(this.isPage);
        console.log(this.next);
      }
  }

}
  

</script>

<style scoped>
  
  .page{
    position: fixed;
    bottom: 30px;
    width: 100%;
    height: 50px;
    text-align: center;
  }
  .page .prev{
    margin-right: 30px;
  }
</style>


