import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/components/index/Index'
import Find from '@/components/find/find'
import Mine from '@/components/mine/mine'
import Order from '@/components/order/order'
import Test from '@/components/Test'
import Goods from '@/components/Goods'
import GoodsItem from '@/components/GoodsItem'
import Zaui from '@/components/Zaui'
Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    { 
      path: '/',
      redirect: '/index',
    },
    { 
      path: '/index',
      name: 'Index',
      component: Index

    },
    { 
      path: '/find',
      name: 'Find',
      component: Find
    },
    { 
      path: '/mine',
      name: 'Mine',
      component: Mine
    },
    { 
      path: '/order',
      name: 'Order',
      component: Order
    },
    { 
      path: '/test',
      name: 'Test',
      component: Test
    },
    { 
      path: '/goods',
      name: 'Goods',
      component: Goods
    },
    { 
      path: '/goodsItem/:typeId',
      name: 'GoodsItem',
      component: GoodsItem
    }
    ,
    { 
      path: '/zaui',
      name: 'Zaui',
      component: Zaui
    }
  ]
})



